import React from 'react';
/** @jsx jsx */
import { jsx } from '@emotion/core';

import { Centric, WideBlock } from '.';
import { Commentizer } from '../contents';
import { Space, Typeface, } from '../primitives';

export function StoryImg(props) {
    let { caption, src } = props;
    return (
        <div css={styles.storyImg}>
            {
                typeof src === 'string' ?
                    <img css={styles.img} src={src} alt={caption} /> :
                    <div css={styles.imgGroup}>
                        {
                            src.map((imgSrc, i) => (
                                <img key={i} src={imgSrc} alt={caption} />
                            ))
                        }
                    </div>
            }
            <Centric>
                <div css={styles.caption}>{Commentizer(caption)}</div>
            </Centric>
        </div>
    )
}

const styles = {
    storyImg: {
        label: 'StoryImg',
        display: 'flex',
        flexDirection: 'column',
    },
    imgGroup: {
        alignItems: 'center',
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'center',
        '&>img': {
            // width: '100%',
            margin: Space['8'],
        }
    },
    img: {
        label: 'Img',
        marginBottom: Space['8'],
        width: '100%',
    },
    caption: [
        {
            label: 'Caption',
            textAlign: 'Center',
        },
        Typeface.comment.darkRegular,
    ]
}