import React from 'react';
/** @jsx jsx */
import { jsx } from '@emotion/core';

import { Centric, MidLinkInvert } from '../components';
import { Commentizer, } from '../contents';
import { BackgroundPatternLight, Color, Space, Typeface, } from '../primitives';

// next
// pre
// back

export function GuideBoard(props) {
    let { next, pre } = props;
    function backToTop(e) {
        e.preventDefault();
        window.scrollTo(0, 0);
    }
    return (
        <div css={[styles.guideBoard, BackgroundPatternLight]}>
            <Centric>
                <div css={styles.boardsWrapper}>
                    <div css={styles.boardGroupWrapper}>
                        <div css={styles.bigBoard}>
                            {next ?
                                <React.Fragment>
                                    <MidLinkInvert link={next.link}>Next Project </MidLinkInvert>
                                    <div css={Typeface.body}>{Commentizer(next.title)}</div>
                                </React.Fragment> :
                                next
                            }
                        </div>
                        <div css={styles.bigBoard}>
                            {pre ?
                                <React.Fragment>
                                    <MidLinkInvert link={pre.link}> Previous Project</MidLinkInvert>
                                    <div css={Typeface.body}>{Commentizer(pre.title)}</div>
                                </React.Fragment> :
                                pre
                            }
                        </div>
                    </div>
                    <div css={styles.boardGroupWrapper}>
                        <div css={styles.smallBoard}>
                            <a css={Typeface.interactableSmallInvert} href="#" onClick={backToTop}>  Bact To Top</a>
                        </div>
                    </div>
                </div>
            </Centric>
        </div>
    )
}

const styles = {
    guideBoard: {
        label: 'GuideBoard',
        alignItems: 'flex-end',
        color: 'white',
        display: 'flex',
        height: 432,
        position: 'relative',
        width: '100%',
        '&::before': {
            backgroundColor: Color.primary,
            content: `''`,
            display: 'block',
            height: '100%',
            left: 0,
            position: 'absolute',
            top: 0,
            width: '100%',
            zIndex: -2,
        }
    },
    boardsWrapper: {
        label: 'Boards',
        display: 'flex',
        justifyContent: 'space-between',
        marginBottom: Space['120'],
    },
    boardGroupWrapper: {
        label: 'BoardGroup',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'flex-end',
        '& > div:not(:last-child)': {
            marginBottom: Space['16'],
        }
    },
    bigBoard: {
        label: 'BigBoard',
    },
    smallBoard: {
        label: 'smallBoard',
        paddingBottom: 32
    }
}