import React from 'react';
/** @jsx jsx */
import { jsx } from '@emotion/core';

import { TinyNavLink } from '.';
// import { Commentizer, } from '../contents';
import { Space, Typeface, } from '../primitives';

export function TopNav(props) {
    const navItems = props.navItems.map((item, i) =>
        <NavItem key={i} comment={item.comment} link={item.link} linkName={item.linkName} rejectRouter={item.rejectRouter ? true : false} />
    );
    return (
        <nav css={styles.topNav}>
            {navItems}
        </nav>
    )
}

const NavItem = props => (
    <div css={styles.item}>
        <TinyNavLink link={props.link} linkName={props.linkName} rejectRouter={props.rejectRouter} />
        <div css={Typeface.comment.darkRegular}>{props.comment}</div>
    </div>
)

const styles = {
    topNav: {
        label: 'TopNav',
        display: 'flex',
        justifyContent: 'flex-end',
        maxWidth: 'inherit',
        position: 'fixed',
        top: 0,
        width: 'inherit',
        zIndex: 2000,
    },
    item: {
        label: 'NavItem',
        display: 'flex',
        flexDirection: 'column',
        height: 72,
        justifyContent: 'flex-end',
        marginRight: Space['40'],
    }
}