import React from 'react';
/** @jsx jsx */
import { jsx } from '@emotion/core';

import { Space, Typeface, } from '../primitives';

export function ProjectInfo(props) {
    let infoCols = props.projectInfo.map((infoItem, i) =>
        <div key={i} css={styles.infoItem}>
            <div css={Typeface.label}>
                {infoItem.label}
            </div>
            <div css={Typeface.bodyS}>
                {infoItem.value}
            </div>
        </div>
    )
    return (
        <div css={styles.projectInfo}>
            {infoCols}
        </div>
    )
}

const styles = {
    projectInfo: {
        label: 'ProjectInfo',
        display: 'flex',
    },
    infoItem: {
        label: 'labelItem',
        marginRight: Space['120'],
    }
}